let editor;
export default class MixedBox extends JXObject {
    constructor(controller, params) {
        super();
        editor = Editor.getInstance();
        this.controller = controller; // ==> Edition.js
        this.commonLib = new Common({ jx: this.jx });
        var item = $(html);
        item.prop("id", params.id);
        this.onChanged = new signals.Signal();
        item[0].addEventListener("change", (event) => {
            this.onChanged.dispatch({ target: this });
        });
        if (params.onChanged) {
            this.onChanged.add(params.onChanged);
        }
        ;
        this._data = params;
        item.data("controller", this);
        this.item = item;
        this.gabarit = gabarit.children;
    }
    get value() {
        //console.log('GETVALUE', this.item[0].value)
        return this.item[0].value;
    }
    get data() {
        // console.log('this.item.find("#value").attr("checked")', this.item.find("#value").prop("checked"))
        return this._data;
    }
}
